'use client'

import { useEffect, useState } from 'react'
import { ChevronDown, ChevronRight, Plus, Pencil, X } from 'lucide-react'

export default function VinculacaoOrganizacionalPage() {
  const [hierarquia, setHierarquia] = useState<any[]>([])
  const [departamentos, setDepartamentos] = useState<any[]>([])
  const [funcoes, setFuncoes] = useState<any[]>([])
  const [usuarios, setUsuarios] = useState<any[]>([])

  const [showModal, setShowModal] = useState(false)
  const [editando, setEditando] = useState<any | null>(null)

  const [form, setForm] = useState({
    vinculacao_id: null as number | null,
    dpto_id: '',
    func_id: '',
    user_id: '',
    percentual_alocacao: 100,
    custo_mensal: '',
  })

  useEffect(() => {
    carregarHierarquia()
    carregarCombos()
  }, [])

  async function carregarHierarquia() {
    const token = localStorage.getItem('entity_token')
    const res = await fetch(
      `${process.env.NEXT_PUBLIC_API_URL}/entity/vinculacao`,
      { headers: { Authorization: `Bearer ${token}` } },
    )
    const json = await res.json()
    setHierarquia(Array.isArray(json) ? json : [])
  }

  async function carregarCombos() {
    const token = localStorage.getItem('entity_token')
    const res = await fetch(
      `${process.env.NEXT_PUBLIC_API_URL}/entity/vinculacao/combos`,
      { headers: { Authorization: `Bearer ${token}` } },
    )
    const json = await res.json()
    setDepartamentos(json.departamentos || [])
    setFuncoes(json.funcoes || [])
    setUsuarios(json.usuarios || [])
  }

  function limparMoeda(valor: string) {
    return valor.replace(/\./g, '').replace(',', '.')
  }

  async function salvar() {
    const token = localStorage.getItem('entity_token')

    const payload = {
      dpto_id: form.dpto_id,
      func_id: form.func_id,
      user_id: form.user_id,
      percentual_alocacao: form.percentual_alocacao,
      custo_mensal: form.custo_mensal
        ? Number(limparMoeda(form.custo_mensal))
        : null,
    }

    const url = editando
      ? `${process.env.NEXT_PUBLIC_API_URL}/entity/vinculacao/${form.vinculacao_id}`
      : `${process.env.NEXT_PUBLIC_API_URL}/entity/vinculacao`

    await fetch(url, {
      method: editando ? 'PUT' : 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(payload),
    })

    setShowModal(false)
    carregarHierarquia()
  }

  return null
}
